import React from 'react';
import './App.css';
import {Switch, Route, HashRouter } from 'react-router-dom';

//Componentes
import CreditoElektraComponent from '../src/components/creditoElektraComponent/creditoElektraComponent.js';
import CreditoDetallesComponent from '../src/components/creditoDetallesComponent/creditoDetallesComponent.js';
import QrComponent from '../src/components/qrComponent/qrComponent.js';
import ErrorCargaComponent from '../src/components/errorCargaComponent/errorCargaComponent.js';
import ErrorConexionComponent from '../src/components/errorConexionComponent/errorConexionComponent.js';
import CreditoInsuficienteComponent from '../src/components/creditoInsuficienteComponent/creditoInsuficienteComponent.js';

 const App = ()=> {
    return (<HashRouter>
			    <Switch>	
				    <Route path="/inicio/:oferta" exact={true} component={ CreditoElektraComponent }/>
				    <Route path='/detalle' exact={true} component={ CreditoDetallesComponent }/>
				    <Route path='/qr' exact={true} component={ QrComponent }/>
				    <Route path='/errorCarga' exact={true} component= { ErrorCargaComponent }/>
				    <Route path='/errorConexion' exact={true} component= { ErrorConexionComponent }/>
            <Route path='/insuficiente' exact={true} component= { CreditoInsuficienteComponent }/>
			    </Switch>
		    </HashRouter>);
}

export default App;