import axios from "axios";

const clienteTokenGobAPIPWA = axios.create({
    baseURL: "https://dev-api.bancoazteca.com.mx:8080/oauth2/v1",
    headers: {
        "Content-Type": "application/x-www-form-urlencoded"
    },
    auth: {
        username: "UpfcsEqOBFmbLQKI4nVBVQlAPoqTiY4T",
        password: "UsQlKnTvRtjApkzV"
    }
});

const obtenerTokenGobAPIPWA = async() => {
    try {
        const resultado = await clienteTokenGobAPIPWA.post(
            "/token",
            "grant_type=client_credentials"
        );
        return resultado.data.access_token;
    } catch (error) {
        console.log("Hubo un problema con la petición: " + error.message);
    }
};


