clienteAPIOfertas.interceptors.response.use(
    response => {
        return response;
    },
    async error => {
        const originalRequest = error.config;
        if (error.response) {
            if (error.response.status === 401) {
                const respuestaToken = await obtenerTokenGobAPIPWA();
                originalRequest.headers.Authorization = 'Bearer ' + respuestaToken;
                return axios(originalRequest);
            }
        }
        return Promise.reject(error);
    }
);